  deleteRFP(id: number) {
    Swal.fire({
      title: "Are you sure?",
      text: "Do you really want to delete this RFP?",
      icon: "question",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes, Delete RFP!",
    }).then((result) => {
      if (result.isConfirmed) {
        const params = new HttpParams()
          .set("id", id)
          .set("username", this.currentUser)
        this.rFPsService
          .deleteRFPsTemporarily(params)
          .pipe(takeUntil(this.destroy$))
          .subscribe({
            next: (res) => {
              this.snackbar.showNotification(
                "snackbar-success",
                "RFP Deleted Successfully"
              );
            },
            error: (err) => {
              this.snackbar.showNotification(
                "snackbar-danger",
                err.message
              );
            },
            complete: () => {
              this.getData(this.currentPage, this.pageSize);
            },
          }),
          Subscription;
      }
    });
  }


  cancelReactivateRFP(rfpCode: any, action: any) {
    let confirmationTitle = "Are you sure?";
    let confirmationText = "Do you really want to ";
    let confirmationButtonText = "Yes, ";

    if (action === 'CANCELED') {
      confirmationText += "cancel this RFP?";
      confirmationButtonText += "Cancel RFP!";
    } else if (action === 'PENDING') {
      confirmationText += "reactivate this RFP?";
      confirmationButtonText += "Reactivate RFP!";
    }

    Swal.fire({
      title: confirmationTitle,
      text: confirmationText,
      icon: "question",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: confirmationButtonText,
    }).then((result) => {
      if (result.isConfirmed) {
        const params = new HttpParams()
          .set("rfpCode", rfpCode)
          .set("status", action);

        this.rFPsService
          .cancelOrReactivateRFP(params)
          .pipe(takeUntil(this.destroy$))
          .subscribe({
            next: (res) => {
              if (action === 'CANCELED') {
                this.snackbar.showNotification(
                  "snackbar-success",
                  "RFP Canceled Successfully"
                );
              } else if (action === 'PENDING') {
                this.snackbar.showNotification(
                  "snackbar-success",
                  "RFP Reactivated Successfully"
                );
              }
              this.getData(this.currentPage, this.pageSize);
            },
            error: (err) => {
              this.snackbar.showNotification(
                "snackbar-danger",
                "Server Error: " + err.message
              );
            },
          });
      }
    });
  }


