import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AllRfpsComponent } from './pages/all-rfps/all-rfps.component';
import { ManageRfpComponent } from './pages/manage-rfp/manage-rfp.component';
import { RoutePrivilegeGuard } from '../../data/services/_AccessControlAuthGuard.service';

const routes: Routes = [


  {
    path: "all-rfps",
    component: AllRfpsComponent,
    canActivate: [RoutePrivilegeGuard],
    data: {  clientName: 'ProcurementModule', requiredPrivilege:["All RFPs"] },
  },

  {
    path: "manage-rfp",
    component: ManageRfpComponent,
    canActivate: [RoutePrivilegeGuard],
    data: {  clientName: 'ProcurementModule', requiredPrivilege:["Manage RFPs"] },
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class RfpsRoutingModule { }
