import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, map } from 'rxjs';
import { environment } from 'src/environments/environment.prod';

@Injectable({
  providedIn: 'root'
})
export class RFPsService {

  constructor(private http: HttpClient) { }

  getRFP(): Observable<any> {
    const RFPUrl = `${environment.baseUrlProcurement}/api/v1/rfp/all`;
    return this.http.get<any[]>(RFPUrl);
  }
  // getRFPById(params: any): Observable<any> {
  //   const RFPUrl = `${environment.baseUrlProcurement}/api/v1/rfp/fetchBy/id`;
  //   return this.http.post<any>(RFPUrl, {params});
  // }

  getRFPById(params: any): Observable<any> {
    return this.http.get<any>(`${environment.baseUrlProcurement}/api/v1/rfp/fetchBy/id`,{params});
  }

  addRFP(data: any): Observable<any> {
    const RFPUrl = `${environment.baseUrlProcurement}/api/v1/rfp/create`;
    return this.http.post<any>(RFPUrl, data);
  }

  updateRFP(RFPDetails: any): Observable<any> {
    const updateBillUrl = `${environment.baseUrlProcurement}/api/v1/rfp/update`;
    return this.http.put<any>(updateBillUrl, RFPDetails)
  }

  updateRFPCommetteeApproval(params: any, approvalAttachments: any): Observable<any> {
    const updateBillUrl = `${environment.baseUrlProcurement}/api/v1/rfp/commetteeApproval`;
    return this.http.put<any>(updateBillUrl, approvalAttachments, { params })
  }


  deleteRFPsTemporarily(params: any): Observable<any> {
    const RFPUrl = `${environment.baseUrlProcurement}/api/v1/rfp/delete`;
    return this.http.delete<any>(RFPUrl, {params});
  } 

  cancelOrReactivateRFP(params: any): Observable<any> {
    const RfqUrl = `${environment.baseUrlProcurement}/api/v1/rfp/cancel`;
    return this.http.post<any>(RfqUrl,{}, {params});
  } 


  getRFPByStatus(params: any): Observable<any> {
    const RFPssUrl = `${environment.baseUrlProcurement}/api/v1/rfp/rfp/status`;
    return this.http.get<any>(RFPssUrl, { params: params, });
  }

 

  validateRFP(statusDetails: any): Observable<any> {
    const API_URL = `${environment.baseUrlProcurement}/api/v1/rfp/verify`;
    return this.http.put(
      API_URL,
      statusDetails
    );
  }


  processSupplierAwardLetter(params: any): Observable<any> {
    const RFPUrl = `${environment.baseUrlProcurement}/api/v1/rfp/send/award`;
    return this.http.post<any>(RFPUrl, {},{params});
  }

  // /api/v1/rfp/send/award/notification
  
  processEmailForNomination(params: any): Observable<any> {
    const RFPUrl = `${environment.baseUrlProcurement}/api/v1/rfp/send/award/notification`;
    return this.http.post<any>(RFPUrl, {},{params});
  }
  
  getRFPPendingCommitee(): Observable<any> {
    const RFPUrl = `${environment.baseUrlProcurement}/api/v1/rfp/find/rfp/pending/commitee/approval`;
    return this.http.get<any[]>(RFPUrl);
  }

    // ********************************************************************************************************************

  donwloadRFPRecipientsReport(params: any): Observable<any> {
    let headers = new HttpHeaders();
    headers.append("Accept", "application/pdf");
    let requestOptions: any = {
      params: params,
      headers: headers,
      responseType: "blob",
      withCredentials: false,
    };
    let API_URL = `${environment.baseUrlProcurement}/api/v1/report/download/rfp/suppliers`;
    return this.http.get(API_URL, requestOptions).pipe(
      map((response: BlobPart) => {
        console.log("Hey this is download RFP recipients Report", response);
        return {
          filename: "Report",
          data: new Blob([response], { type: "application/pdf" }),
        };
      })
    );
  }
  donwloadRFPItemsReport(params: any): Observable<any> {
    let headers = new HttpHeaders();
    headers.append("Accept", "application/pdf");
    let requestOptions: any = {
      params: params,
      headers: headers,
      responseType: "blob",
      withCredentials: false,
    };
    let API_URL = `${environment.baseUrlProcurement}/api/v1/report/download/rfp/items`;
    return this.http.get(API_URL, requestOptions).pipe(
      map((response: BlobPart) => {
        console.log("Hey this is download RFP Items Report", response);
        return {
          filename: "Report",
          data: new Blob([response], { type: "application/pdf" }),
        };
      })
    );
  }


}
